/* --- 01 TYPOGRAPHY SYSTEM

      FONT WEIGHT
       Default: 400
      LINE HEIGHTS
      Default:1
      small: 1.05
      paragraph default: 1.6
      FONT SIZE SYSTEM (px)
      10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

      --- 02 COLORS

      Primary: #e67e22

      -Tints: #fdf2e9
      -Shades: #cf711f
      -Accents: 
      -Greys:#555, #333

    --- 05 SHADOWS


    --- 06 BORDER RADIUS


    --- 07 WHITESPACE
     SPACING SYSTEM (px)
      2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128
      */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

body {
  background-color: rgb(255, 255, 255);
  color: rgb(51 51 51);
  line-height: 1.4285;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(204, 0, 0);
  color: white;
  font-size: 1.2rem;
  padding: 0.66rem 4rem;
  line-height: 1.7rem;
  min-height: 5rem;
}

.header-left-container {
  position: relative;
  text-decoration: none;
  color: white;
}

.header-store-info {
  color: rgb(247, 247, 247);
  letter-spacing: 0.009rem;
}

.header-my-store {
  color: rgb(247, 247, 247);
  margin-right: 0.7rem;
  word-spacing: 0.01rem;
}

#header-store-info-dot {
  position: absolute;
  left: 5.1rem;
  top: -0.1rem;
  font-size: 1.8rem;
  font-weight: bold;
  color: rgb(247, 247, 247);
}

.header-nav {
  display: flex;
  grid-gap: 2.7em;
  gap: 2.7em;
}

.header-category {
  color: rgb(247, 247, 247);
  text-decoration: none;
}

.store-closing-time {
  color: rgb(247, 247, 247);
  text-decoration: none;
}

#store-name {
  display: block;
  font-size: 1.4rem;
  font-weight: 700;
  padding-right: 1rem;
  color: rgb(247, 247, 247);
  text-decoration: none;
}

#store-name-text {
  position: relative;
}

.arrow-btn {
  max-width: 1.7rem;
  position: absolute;
  right: -1.9rem;
  top: -0.1rem;
}

.header-category:hover {
  cursor: pointer;
  text-decoration: underline;
}

.header-store-info:hover {
  cursor: pointer;
  text-decoration: underline;
  color: white;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(253, 254, 255);
  font-size: 1.6rem;
  padding: 1.7rem 3.1rem;
  border-bottom: red;
  height: 8rem;
  box-shadow: 0 0.1rem 0.4rem #e3e3e3;
}
.nav-category {
  background-color: rgb(255, 255, 255);
  font-size: 1.6rem;
  font-weight: bold;
  border: none;
  max-width: 20rem;
  padding: 1.5rem 0.1rem;
  border-radius: 0.5rem;
  text-decoration: none;
  color: black;
}

.nav-categories-container {
  display: flex;
  margin-left: 5.3rem;
  grid-gap: 4.7rem;
  gap: 4.7rem;
}

.nav-category {
  color: black;
}

.nav-category:hover {
  cursor: pointer;
  background-color: rgb(244, 244, 244, 0.6);
}

.nav {
  display: flex;
  align-items: center;
  grid-gap: 4.7rem;
  gap: 4.7rem;
}

#categories-btn {
  margin-left: -1.2rem;
}

.logo {
  max-width: 5.5rem;
  position: absolute;
  top: 6.1rem;
  left: 3.4rem;
}
.search-box-container {
  display: flex;
  align-items: center;
  margin: 0;
}

.search-form {
  position: relative;
}

.search-input {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: rgba(204, 0, 0);
  border: solid rgb(247, 247, 247);
  background-color: #f6f6f6;
  border-radius: 0.8rem;
  height: 4.4rem;
  min-width: 52.1rem;
  max-width: 52.1rem;
  font-size: 1.6rem;
  padding: 0.8rem;
  outline: none;
}

.search-input::-webkit-input-placeholder {
  color: #646464;
  padding-left: 0.3rem;
}

.search-input::placeholder {
  color: #646464;
  padding-left: 0.3rem;
}

.searchBtn {
  position: absolute;
  right: 0.01rem;
  bottom: 0.3rem;
  max-width: 3.9rem;
}

.searchBtn:hover {
  cursor: pointer;
}
.sign-in-shopping-cart-container {
  display: relative;
}

.guest-image {
  position: absolute;
  right: 18.3rem;
  padding-right: -1rem;
}

.shopping-cart {
  max-width: 3.8rem;
  position: absolute;
  right: 4.2rem;
}

.shopping-cart:hover {
  cursor: pointer;
  background-color: rgb(244, 244, 244, 0.6);
}

#sign-in-btn {
  position: absolute;
  right: 13rem;
  font-size: 1.4rem;
  margin-left: -3.9rem;
  text-decoration: none;
  color: black;
}

#sign-in-btn:hover {
  cursor: pointer;
  background-color: rgb(244, 244, 244, 0.6);
}

.product-header-container {
  margin: 2rem 3.5rem;
}

.product-path {
  font-size: 1.4rem;
  color: rgb(104, 104, 104);
}

.product-anchor {
  color: rgb(104, 104, 104);
  text-decoration: underline;
}

.product-anchor:hover {
  color: black;
  text-decoration: none;
}

.shop-classics {
  display: block;
  font-size: 1.4rem;
  color: rgb(104, 104, 104);
  text-decoration: underline;
  margin: 2rem 0;
}

.shop-classics:hover {
  color: black;
  text-decoration: none;
}

.product-header {
  margin: -1.8rem 0;
  font-weight: bold;
  font-size: 2.9rem;
}

.product-sidebar-price-container {
  position: absolute;
  right: -40rem;
  top: -15rem;
}

.product-sidebar-price-container-child {
  display: flex;
  align-items: center;
}

.product-price {
  color: rgba(204, 0, 0);
  font-size: 2.3rem;
}

.sale {
  font-weight: 350;
  font-size: 1.4rem;
}

.reg-product-price {
  font-size: 1.2rem;
  font-weight: 400;
  color: rgb(105, 105, 105);
}

.purchased-online {
  position: relative;
  font-size: 1.2rem;
  line-height: 2.5rem;
  color: rgb(51, 51, 51);
}

#info-btn {
  position: absolute;
  top: 0.3rem;
  margin-left: 0.4rem;
  max-width: 1.9rem;
}

.stars {
  max-width: 10rem;
}

.ratings {
  display: flex;
  align-items: center;
}

.ratings:hover {
  text-decoration: none;
}

.ratings-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  position: absolute;
  top: 6rem;
  right: 22rem;
  grid-gap: 0.2rem;
  gap: 0.2rem;
  color: rgb(105, 105, 105);
  font-size: 1.2rem;
  line-height: 2rem;
  padding-right: 0.4rem;
}

#grey-arrow {
  max-width: 1.6rem;
}

.questions {
  font-size: 1.2rem;
  color: rgb(105, 105, 105);
  position: absolute;
  top: 5.5rem;
  right: 15.4rem;
  border-left: 1px solid rgb(214, 214, 214);
  display: inline-block;
  margin-left: 0.4rem;
  padding-left: 0.4rem;
}

.questions:hover {
  color: black;
  text-decoration: none;
}

.color-pallete-container {
  position: absolute;
  top: 10rem;
  font-weight: lighter;
}

.pallete-header-container {
  display: flex;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}

.pallete-header {
  font-size: 1.6rem;
  font-weight: 350;
}

#item-color {
  font-size: 1.6rem;
  font-weight: bold;
}

.product-photos-container {
  display: flex;
  align-items: center;
  position: relative;
}

.product-photo-sidebar {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.main-product-img-container {
  margin: 1.2rem 3.6rem;
  display: inline-block;
  border: 1px solid black;
}

.main-product-img {
  padding: 3rem 0;
  max-width: 14rem;
  max-height: 13.7rem;
}

.second-product-img {
  margin: 0.1rem 3.6rem;
  max-width: 14rem;
}

.third-product-img {
  margin: 0.3rem 3.6rem;
  max-width: 14rem;
  min-height: 14rem;
}

.fourth-product-img {
  margin: 0.3rem 3.6rem;
  max-width: 14rem;
}

.fifth-product-img {
  margin: 0.3rem 3.6rem;
  max-width: 14rem;
}

.fifth-product-img:hover {
  cursor: pointer;
}

.hero-product-img {
  max-width: 74rem;
  margin: 0 -2.4rem;
}
.hero-product-img-price-container {
  position: relative;
